import React, { useState } from "react";
import { TextField, Button, Typography, Container, CircularProgress } from '@mui/material';
import axios from "axios"; //    axios

const ChangePassword = () => {
  //     
  const [userId, setUserId] = useState("");
  const [token, setToken] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);


  //     API
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      //      POST
      //const response = await axios.post("https://api.example.com/change-password", {
      const response = await axios.post("https://quizappsyria.pythonanywhere.com/users/reset_password_confirm/", {
        uid: userId,
        token: token,
        new_password: newPassword,
      });

      //       
      setMessage("    ");
    } catch (error) {
      //       
      setMessage("     .    .");
    }
  };

  return (
    <Container maxWidth="xs">
    
    <Typography variant="h4" align="center" gutterBottom>
        Change Password
      </Typography>
      <form onSubmit={handleSubmit}>
        {/* <div>
          <label>  (ID):</label>
          <input
            type="text"
            value={userId}
            onChange={(e) => setUserId(e.target.value)}
            required
          />
        </div> */}
           <TextField
          label="ID"
          type="text"
          variant="outlined"
          fullWidth
          margin="normal"
          value={userId}
          onChange={(e) => setUserId(e.target.value)}
          required
        />
        <TextField
          label="token"
          type="text"
          variant="outlined"
          fullWidth
          margin="normal"
          value={token}
          onChange={(e) => setToken(e.target.value)}
          required
        />
        {/* <div>
          <label> (Token):</label>
          <input
            type="text"
            value={token}
            onChange={(e) => setToken(e.target.value)}
            required
          />
        </div> */}
        {/* <div>
          <label>  :</label>
          <input
            type="password"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            required
          />
        </div> */}
         <TextField
          label="New Password"
          type="password"
          variant="outlined"
          fullWidth
          margin="normal"
          value={newPassword}
          onChange={(e) => setNewPassword(e.target.value)}
          required
        />
        <Button 
         type="submit"
         variant="contained"
         color="primary"
         fullWidth
         disabled={loading}
         style={{ marginTop: '16px' }}
        
        >
        {loading ? <CircularProgress size={24} /> : 'Change Password'}
        </Button>
      </form>
      {message && <p>{message}</p>}    
    </Container>
  );
};

export default ChangePassword;
